.paper {
    background-color: var(--bgBody) !important;
    border-radius: 10px;
}

.divider {
    border-color: var(--colorSecond) !important;
}

.eat-card {
    cursor: pointer;
    border-bottom: 1px solid var(--colorSecond) !important;
    padding-bottom: 1.75rem !important;
    padding: 2rem 1rem;
}
.eat-card:last-of-type {
    border-bottom: none !important;
}

.eat-card * {
    cursor: pointer;
}
.eat-card:hover {
    background-color: var(--bgSecond) !important;
}

.chip-rate span {
    padding-left: 8px !important;
}

.MuiChip-filled span {
    color: var(--colorWhite) !important;
}

.MuiButton-startIcon svg path {
    color: var(--colorWhite) !important;
    background-color: var(--colorWhite) !important;
    fill: var(--colorWhite) !important;
}

.container {
    background-color: var(--bgSecond) !important;
    border-radius: 10px;
}

.MuiRating-root label span path {
    color: #faaf00 !important;
    fill: #faaf00 !important;
}

.MuiInputLabel-formControl {
    color: var(--color);
}
.ratingForm .MuiOutlinedInput-root {
    color: var(--color) !important;
    background-color: var(--bgBody) !important;
}
.ratingForm fieldset {
    border: 0px solid var(--bgBody) !important;
}
textarea {
    color: var(--color) !important;
}

.MuiAlert-action path {
    color: var(--colorInvert) !important;
    background-color: var(--colorInvert) !important;
    fill: var(--colorInvert) !important;
}

.closeButton path {
    color: var(--colorBlack) !important;
    fill: var(--colorBlack) !important;
}

.good-comment {
    background-color: var(--buttonColorTopComment) !important;
}
.med-comment {
    background-color: var(--buttonColorMedComment) !important;
}
.bad-comment {
    background-color: var(--buttonColorBadComment) !important;
}

.title {
    cursor: pointer;
    transition: all 0.2s ease;
}
.title:hover {
    background-color: #45527438 !important;
}

.addForm input {
    color: var(--color) !important;
}
.addForm .MuiInputBase-root {
    background-color: var(--bgSecond) !important;
}
.addForm legend, label {
    color: var(--helpColor) !important;
}

.aboutPage h6 {
    font-weight: 300 !important;
}